body {
    margin: 0;
    background-color: #20262F;
  }

  h1, h2, h3, h4, h5, p, ul, li {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }

  a {
    text-decoration: none;
    color: rgb(124, 124, 124);
  }

  a:hover {
    color: #fff;
    transition-duration: 0.6s;
  }
  
  img {
    margin-top: 40px;
    margin-bottom: 40px;
    width: 900px;
  }

  #img-logo {
    width: 700px;
  }
  
  h2 {
    font-size: 20px;
    font-weight: 800px;
    color: #fff;
  }

  h3 {
    
    font-size: 20px;
    line-height: 20px;
    font-weight: 400px;
    color: #636363;
  }

  h4 {
    font-size: 20px;
    font-weight: 100px;
  }

  h5 {
    font-size: 12px;
    color: rgb(54, 54, 54);
  }

  h1 {
    margin-top: 2em;
    font-size: 40px;
    font-weight: 600;
    width: 800px;
    color: rgb(255, 255, 255);
    line-height: 20px;
    margin-left: auto;
    margin-right: auto;
    letter-spacing: normal;
  }
  .content {
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }
  .active {
    color: rgb(255, 255, 255);
  }
  #main {
    width: 900px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
  }
  #container {
      margin: 10px auto;
      /* position: relative; */
      /* display: flex; */
      /* flex-wrap: wrap; */
      /* width: 800px; */
      /* height: 700px; */
      /* background-color: black; */
      /* margin-left: auto; */
      /* margin-right: auto; */
      padding: 5px;
  }

  .el {
    width: 380px;
    height: 380px;
    margin: 10px;
    background-color: rgb(0, 0, 0);
    transition: .5s ease;
    
  }


  /* .sub_el {
    width: 380px;
    height: 380px;
    margin: 10px;
    background-color: rgb(0, 0, 0);
    display: none;

  } */

  .el:hover {
    opacity: 0.8;
    /* display:block; */
  }


  .menu {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    float: right;
    margin-top: 20px;
    font-weight: 700;
    font-size: 0.9em;
    word-spacing: 2em;    
  }

  /* .menu a {
    color: rgb(206, 7, 7);
    word-spacing: 2em;
  } */

  .logo {
    display: inline-block;
    letter-spacing: -0.8px;
    color: rgb(255, 255, 255);
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
  }

  .header {
    width: 900px;
    display: inline-block; 
  }

  #up_margin {
    height: 30px;
  }

  #down_margin {
    height: 100px;
  }

p {
  color: rgb(210, 210, 210);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 0.9rem;
  line-height: 1.3rem;
}

#unselect {
  margin-top: 40px;
  margin-bottom: 40px;
  -webkit-user-select: none; /* Safari */        
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

#me {
  display: table-cell;
  background: url('./img/me.png') no-repeat;
  background-size: 410px 410px;
  width: 410px;
  height: 410px;
  padding: 20px;
}

#table {
  display: table;
}

#table a {
  display: inline-block;
  
}

#cell {
  display: table-cell;
  padding: 20px;
}

#button_left {
  display: table-cell;
  border-radius: 24px;
  border: 2px solid rgb(236, 236, 236);
  width: 120px;
  height: 40px;
  text-align: center;
  vertical-align: middle;
  line-height: 40px;
  color: rgb(255, 255, 255);
  font-weight: 500;
}

#button_left:hover {
  border-radius: 24px;
  background: rgb(165, 165, 165);
  color: #fff;

}

#linkedin {
  display: table-cell;
  background: url('./img/linkedin.png') no-repeat;
  background-size: 40px 40px;
  height: 40px;
  width: 40px;
}

#info {
  padding: 0.5em;
  margin: 2em;
  background-color: rgb(255, 249, 217);
}

.a_color {
  color: rgb(255, 255, 255);
  font-weight: 600;
  text-decoration: underline;
}

.a_tab {
  margin-right: 20px;
}

.indent {
  /* text-indent: -1em; */
  margin-top: 0em;
  margin-bottom: 0em;

}

ul {
  list-style-type: square; /* Маркеры в виде квадрата */
  list-style-position: outside;
  list-style-type: none;
  padding: 0;
}

ol {
  /* margin: 0;  */
  padding-top: 0.5em;
  margin-top: 0em;
  margin-bottom: 0em;
  line-height: 0.9em;
}


#mainBLock {
  margin: 20px auto;
  width: 700px;
  height: 300px;
}

#maintext {
  color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 600;
  text-align: center;
  padding-top: 100px;
  font-size: 40px;
}

#maintext_small {
  color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  text-align: center;
  font-size: 18px;
}
